.cards {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(1, $card-width);
  gap: 46px 48px;

  @media screen and (min-width: 488px) {
    grid-template-columns: repeat(2, $card-width);
  }

  @media screen and (min-width: 768px) {
    grid-template-columns: repeat(3, 200px);
  }

  @media screen and (min-width: 1024px) {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  width: $card-width;
  padding: 32px 16px 16px;

  border: 1px solid $white-90;
  border-radius: 5px;

  transition: scale 0.3s;

  &__img {
    display: block;

    height: 134px;
    max-width: 160px;

    margin: 0 auto 40px;
  }

  &__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;

    color: $black;

    margin-bottom: 4px;

    transition: color $transition-duration;
  }

  &__id {
    font-size: $font-xs;
    line-height: 14px;

    color: $gray;

    margin-bottom: 16px;
  }

  &__rate {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 24px;
  }

  &__reviews {
    font-size: $font-xs;
    color: $black;
    text-decoration: none;
    line-height: 14px;

    align-self: flex-end;
  }

  &__price {
    display: flex;
    justify-content: space-between;

    margin-bottom: 16px;
  }
  &__text {
    font-size: $font-s;
    line-height: 18px;
    color: $gray;
  }

  &__value {
    font-size: $font-l;
    font-weight: 700;
    line-height: 18px;
    color: $black;
  }

  &__button {
    width: 100%;

    padding: 11px;

    font-family: inherit;
    text-align: center;
    text-transform: uppercase;

    border: 1px solid transparent;
    border-radius: 5px;

    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    color: $white-100;

    background-color: $blue;

    transition: background-color $transition-duration;

    cursor: pointer;

    &:hover {
      background-color: $white-100;
      color: $blue;
      border-color: $blue;
    }
  }
  &:hover {
    scale: 1.2;
  }
  &:hover &__title {
    color: #34568b;
  }
}
