* {
  box-sizing: border-box;
  margin: 0;
}

body {
  padding: 0;
}

ul {
  padding: 0;
  list-style: none;
}

.container {
  padding: 50px 40px;
}

.cards {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  display: grid;
}

@media screen and (width >= 488px) {
  .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) {
  .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .cards {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: scale .3s;
}

.card__img {
  max-width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__id {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  align-self: flex-end;
  font-size: 10px;
  line-height: 14px;
  text-decoration: none;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  width: 100%;
  padding: 11px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: background-color .3s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

.card:hover {
  scale: 1.2;
}

.card:hover .card__title {
  color: #34568b;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  text-transform: uppercase;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  height: 100%;
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  display: block;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}
